import CustomInput from './CustomInput.vue'
import SearchSelect from './SearchSelect.vue'
import CustomTabs from './CustomTabs.vue'
import tagsSearchSelect from './tagsSearchSelect.vue'
import lineChartPercents from './charts/lineChartPercents.vue'
import lineChartTemperature from './charts/lineChartTemperature.vue'

export {
  CustomInput,
  SearchSelect,
  CustomTabs,
  tagsSearchSelect,
  lineChartPercents,
  lineChartTemperature,
}

export default {
  install(Vue) {
    Vue.component('CustomInput', CustomInput)
    Vue.component('SearchSelect', SearchSelect)
    Vue.component('CustomTabs', CustomTabs)
    Vue.component('tagsSearchSelect', tagsSearchSelect)
    Vue.component('lineChartPercents', lineChartPercents)
    Vue.component('lineChartTemperature', lineChartTemperature)
  },
}
